.play {
  height: 88vh;
  overflow: scroll;
}

.play::-webkit-scrollbar {
    display: none;
}

.player-main {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 0;
  justify-content: space-around;
  align-content: start;
  margin: 1vh 2% 0;
}


@media screen and (max-width: 1024px){

    .play {
        height: 91vh;
        overflow: scroll;
    }
}
