    -webkit-text-fill-color: transparent;
  }

  .search-button:before {
    content: "\f002";
    font-family: FontAwesome;
    font-size: 2vw;
    color: $input-search-img;
    display: flex;
    margin-left:  -0.1vw;
  }

  .search-button {
